'use server'

import { adminDb } from "@/lib/firebase_admin"
import { cookies } from 'next/headers'
import { redirect } from 'next/navigation'

interface ConsultationData {
    name: string
    email: string
    phone: string
    service: string
    description?: string
    status: 'pending' | 'completed'
    createdAt: string
}

export async function createConsultation(formData: FormData): Promise<{
    success: boolean;
    id?: string;
    error?: string
}> {
    const rawPhone = formData.get('phone') as string
    const cleanedPhone = rawPhone.replace(/[\s\-\(\)]/g, '')

    try {
        if (!formData.get('email') || !formData.get('phone')) {
            throw new Error('Email y teléfono son requeridos')
        }

        const consultation: ConsultationData = {
            name: formData.get('name') as string,
            email: formData.get('email') as string,
            phone: formData.get('phone') as string,
            service: formData.get('service') as string,
            description: formData.get('description') as string || '',
            status: 'pending',
            createdAt: new Date().toISOString()
        }

        if (!cleanedPhone) {
            throw new Error('El teléfono es requerido')
        }

        const phoneRegex = /^\+?\d{8,15}$/

        if (!phoneRegex.test(cleanedPhone)) {
            throw new Error('Formato inválido. Ejemplos válidos:\n+34612345678\n912345678')
        }

        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        if (!emailRegex.test(consultation.email)) {
            throw new Error('Formato de email inválido')
        }

        const docRef = await adminDb.collection('consultations').add(consultation)
        return { success: true, id: docRef.id }

    } catch (error: any) {
        console.error('Error detallado:', error)
        return {
            success: false,
            error: error.message || 'Error desconocido al crear la consulta'
        }
    }
}

export async function handleAdminLogin(
    prevState: { error?: string },
    formData: FormData
) {
    try {
        const password = formData.get('password')?.toString()

        if (!password) {
            return { error: 'Contraseña requerida' }
        }

        if (password !== process.env.ADMIN_PASSWORD) {
            return { error: 'Contraseña incorrecta' }
        }

        const cookieStore = await cookies();
        cookieStore.set('admin-authenticated', 'true', {
            httpOnly: true,
            secure: process.env.NODE_ENV === 'production',
            maxAge: 60 * 60 * 24 * 7,
            path: '/admin',
        })

        redirect('/admin')

    } catch (error) {
        if (error instanceof Error && error.message.startsWith('NEXT_REDIRECT')) {
            throw error;
        }
        return { error: 'Error del servidor' };
    }
}